.descriptionBlock {
  width: 1000px;
  padding-left: 10px;
  font-weight: 600;
}
.mainPhoto {
  width: 300px;
  height: 300px;
  border: 5px solid rgb(221, 93, 215);
  border-radius: 50px;
  overflow: hidden;
}
.photo {
  width: 300px;
  height: 300px;
  display: block;
  margin: auto;
}

.item img {
  width: 100%;
  height: 200px;
}

.contact {
  padding-left: 100px;
}

.input {
  display: none;
}
.label {
  border: 3px solid #ddec0a;
  border-radius: 5px;
  display: inline-block;
  padding: 6px 100px;
  cursor: pointer;
}
